*{
  box-sizing:border-box;
  margin:2px;
  background-color: black;
  color:white;
 
}
.container{
  background-image: url("./images/crypto.jpg");
  background-size: 100%;
}
@keyframes loader{
  from{color:gray;
  opacity: 0.8;
  }
  to{color:white;
  opacity:1;}
}
h2{
  animation-name: loader;
  animation-duration:2s;
}

.coin-text{
  
  margin:10px;
  padding:10px;
}
.coin-search
{
  opacity:0.8;
  padding: 10px;
}
.coin-search h1{
  background-color:#080808;
  padding:5px;
}

.container{
  background-color: #2b2b2b;
}
.coin-input{
  margin-left:10px; 
  margin-bottom: 5px;
 
}
@media(min-width:768px){
  *{
    font-size: 16px;
  }
  .coin-search p{
    margin-left: 10px;
    font-size: 1px !important;
  }
}


@media(max-width:767px){
  *{
    font-size: 11px;
  }
  .coin-search p{
    margin-left: 10px;
    font-size: 10px !important;
  }
}

