


@media(min-width:767px){
    .coin-cointainer{
        /* display:flex; */
        justify-content:center;
        
    }
    
    .coin-row{
        display:flex;
        flex-direction: row;
        justify-items: start;
        align-items: center;
        height:80px;
        border-bottom:1px solid #d7d7d7;
    }
    
    .coin{
        display: flex;
        align-items: center;
        padding-right:24px;
        min-width: 250px;
    }
    
    .coin h1{
        font-size: 16px;
        /* width: 150px; */
        
    }
    
    .coin img{
        height:30px;
        width:30px;
        margin-right: 10px;
    }
    
    .coin-symbol{
        text-transform: uppercase;
    }
    
    .coin-data{
        /* display:flex; */
        
        text-align:center;
        justify-content: space-between;
        padding:2px;
        width:100%;
    }
    
    .coin-price{
        
        margin:10px;
        width:110px;
    }
    
    .coin-volume{
        margin:10px;
        width:150px;
    }
    
    .coin-marketcap{
        margin:20x;
        width:220px;
    }
    
    .coin-pecent{
        margin:5px;
        width:100;
    }
    
    .red{
        color:#f00606
    }
    
    .green{
        color:#11d811
    }
}

